import React from 'react'
import Badge from './Badge';
export default function Skill() {
    return (
        <div className="skills-main">
            <div id="sk-bg">

            
            <div id="skills-head">
                <h1 className="text-align">Skills, I Have ;</h1>
            </div>
            
            <div className="badge-class">
            <Badge image="c.png" name="C++"/>
            <Badge image="bootstrap-4.svg" name="Bootstrap"/>
            <Badge image="css.png" name="CSS"/>
            <Badge image="javascript.png" name="Javascript"/>
            <Badge image="jquery-icon.svg" name="jQuery"/>
            <Badge image="mongodb-icon.svg" name="MongoDB"/>
            <Badge image="nodejs.svg" name="NodeJs"/>
            <Badge image="photoshop.png" name="PhotoShop"/>
            <Badge image="react.png" name="React"/>
            </div>
            </div>
        </div>
    )
}
